import { MessageSquare, Send } from 'lucide-react';

export default function ReplyComposer({ 
  replyText, 
  setReplyText, 
  onSendReply, 
  onShowAiPanel,
  showSuggestions,
  smartSuggestions 
}) {
  return (
    <div className="p-4 border-t border-gray-200 bg-white">
      {showSuggestions && (
        <div className="mb-3 flex flex-wrap gap-2">
          {smartSuggestions.map((suggestion, index) => (
            <button 
              key={index}
              onClick={() => setReplyText(replyText + "\n\n" + suggestion)}
              className="text-xs bg-blue-50 text-blue-700 px-3 py-1.5 rounded-full hover:bg-blue-100 transition-colors"
            >
              + {suggestion}
            </button>
          ))}
        </div>
      )}
      
      <div className="flex items-start">
        <div className="flex-1 border border-gray-300 rounded-lg overflow-hidden">
          <textarea
            value={replyText}
            onChange={(e) => setReplyText(e.target.value)}
            placeholder="Type your reply..."
            className="w-full p-3 focus:outline-none min-h-[120px] resize-none"
            rows={5}
          />
          
          <div className="flex justify-between items-center px-3 py-2 bg-gray-50 border-t border-gray-200">
            <div className="flex">
              <button 
                onClick={onShowAiPanel}
                className="text-blue-600 hover:text-blue-800 flex items-center text-sm mr-4"
              >
                <MessageSquare className="h-4 w-4 mr-1" />
                Ask AI Copilot
              </button>
            </div>
            
            <button
              onClick={onSendReply}
              disabled={!replyText.trim()}
              className={`px-4 py-1.5 rounded-md flex items-center text-sm ${
                replyText.trim() 
                  ? 'bg-blue-600 text-white hover:bg-blue-700' 
                  : 'bg-gray-200 text-gray-500 cursor-not-allowed'
              }`}
            >
              <Send className="h-4 w-4 mr-1" />
              Send Reply
            </button>
          </div>
        </div>
      </div>
    </div>
  );
} 